import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { Observable } from 'rxjs';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { PortalService } from '../../service/portal.service';

@Component({
    selector: 'app-welcome',
    templateUrl: './welcome.component.html',
    styleUrls: ['./welcome.component.sass'],
})
export class WelcomeComponent implements OnInit {
    commandBarButtons: CommandBarButton[] = [];

    scopeName = 'portal.Landing.Welcome';

    testDate = (new Date()).getTime();

    constructor(
            private readonly _cookieService: CookieService,
            private readonly _portalService: PortalService,
            private readonly _router: Router) {

        this._portalService.setPageTitle(this.scopeName + '.title');

        this.commandBarButtons.push({
            content: { 
                scopeName: this.scopeName,
                label: 'btn.continue'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                });
                return observable;
            }
        });
    }

    ngOnInit() {
        this.getLanguage();
     }

    private getLanguage(): string {
        const lang = this._cookieService.get('preferedLanguage');
        if (!lang || lang === 'en_gb') {
            return 'en';
        } else {
            return 'zh';
        }
    }

    onContinueButtonClick() {
        this._router.navigate(['/landing/step1']);
    }

}
