import { NgModule } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { PortalCommonModule } from '../common/portal-common.module';
import { DirectAccessGuard } from '../route-guard/direct-access.guard';
import { LanguageService } from '../service/language.service';
import { ScheduleService } from '../service/schedule/schedule.service';
import { LandingRoutingModule } from './landing-routing.module';
import { MaintenanceComponent } from './maintenance/maintenance.component';
import { Step1Component } from './step1/step1.component';
import { Step2Component } from './step2/step2.component';
import { TimeoutComponent } from './timeout/timeout.component';
import { WelcomeComponent } from './welcome/welcome.component';

@NgModule({
    declarations: [
        WelcomeComponent,
        Step1Component,
        Step2Component,
        MaintenanceComponent,
        TimeoutComponent,
    ],
    imports: [
        PortalCommonModule,

        LandingRoutingModule,
    ],
    providers: [
        ScheduleService,
        DirectAccessGuard
    ]
})
export class LandingModule {
    constructor(private _translateService: TranslocoService,
        private _languageService: LanguageService) {
        this._languageService.register(this._translateService);
    }
}
