import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslocoService } from '@ngneat/transloco';
import { Observable } from 'rxjs';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { SplashScreenConfig, SplashScreenPosition, SplashScreenStatus } from '../../common/splash-screen/splash-screen.component';
import { PortalService } from '../../service/portal.service';
import { ScheduleService } from '../../service/schedule/schedule.service';

@Component({
    selector: 'app-step1',
    templateUrl: './step1.component.html',
    styleUrls: ['./step1.component.sass']
})
export class Step1Component implements OnInit {

    scopeName = 'portal.Landing.Step1';

    data: { numOfWorkingDay: number };

    commandBarButtons: CommandBarButton[] = [];

    splashScreenConfig: SplashScreenConfig;

    constructor(
        private readonly _router: Router,
        private readonly _portalService: PortalService,
        private readonly _translate: TranslocoService,
        private readonly _scheduleService: ScheduleService) {

        this._portalService.setPageTitle(this.scopeName + '.title');

        this.data = {
            numOfWorkingDay: 0
        };

        this.splashScreenConfig = {
            position: SplashScreenPosition.Fixed,
            message: {
                scopeName: this.scopeName,
                label: 'loading'
            },
            visibility: true,
            status: SplashScreenStatus.Loading
        }

        this.commandBarButtons.push({
            content: {
                scopeName: this.scopeName,
                label: 'btn.acceptTerms'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                })
                return observable;
            }
        }, {
            content: {
                scopeName: this.scopeName,
                label: 'btn.back'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onBackButtonClick();
                    observer.next();
                })
                return observable;
            }
        });
    }

    ngOnInit() {
        this._scheduleService.getBookingPeriod().subscribe({
            next: (bookingPeriod: number) => {
                this.data.numOfWorkingDay = bookingPeriod;
                this.splashScreenConfig.visibility = false;
            },
            error: (errorCode: string) => {
                this.splashScreenConfig.status = SplashScreenStatus.Error;
                this.splashScreenConfig.message = {
                    scopeName: this.scopeName,
                    label: 'lbl.systemBusy',
                    params: { errorCode: errorCode }
                };
            }
        });
    }

    onBackButtonClick() {
        this._router.navigate(['/landing/welcome']);
    }

    onContinueButtonClick() {
        this._router.navigate(['/landing/step2']);
    }
}
