import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { PortalService } from '../../service/portal.service';

@Component({
    selector: 'app-step2',
    templateUrl: './step2.component.html',
    styleUrls: ['./step2.component.sass']
})
export class Step2Component implements OnInit {
    scopeName = 'portal.Landing.Step2';
    selectedAction: string = '';
    commandBarButtons: CommandBarButton[] = [];

    action(selectedAction: string) {
        this.selectedAction = selectedAction;
    }

    constructor(
            private _portalService: PortalService,
            private _router: Router) {
        this._portalService.setPageTitle(this.scopeName + '.title');

        this.commandBarButtons.push({
            content: {
                scopeName: this.scopeName,
                label: 'btn.continue'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.back'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onBackButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.abort'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onAbortButtonClick();
                    observer.next();
                })
                return observable;
            }
        });
    }

    ngOnInit() { }
    
    onAbortButtonClick() {
        this._router.navigate(['/landing/welcome']);
    }
    
    onBackButtonClick() {
        this._router.navigate(['/landing/step1']);
    }

    onContinueButtonClick() {
        this._router.navigate(['/' + this.selectedAction + '/']);
    }
    
}
